.project {
  padding: 0.5em;
  border-radius: 12px;
  background-color: var(--clr-bg-alt);
  margin: 0 auto;
  text-align: center;
  box-shadow: var(--shadow);
  transition: transform 0.2s linear;
}

.project:hover {
  transform: translateY(-7px);
}

.project__description {
  max-width: fit-content;
  margin: 1em auto 2em;
}

.project__tag {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 1.2em 0;
}

.project__tag-item {
  background-color: var(--clr-bg);
  padding: 0.4em 1em;
  border-radius: 5em;
  margin: 0.5em;
  font-size: 0.8rem;
  font-weight: 600;
  color: var(--clr-fg-alt);
}

.project .link--icon {
  margin-left: 0.5em;
}

.project img{
  margin: 0 auto 2em;
  max-height: 100%;
  max-width: 100%;
  border-radius: 10px 10px 4px 4px;
}